.wrap {
  width: 100%;
}

.container {
  max-width: 1000px;
  width: 100%;
  margin: 20px auto;
}
.btn {
  display: flex;
  justify-content: flex-end;
}

.usersBlock {
  height: 50vh;
  max-width: 600px;
  max-width: 100%;
  overflow-y: auto;
  margin: 30px 0;
}
.userItem {
  display: flex;
  padding: 10px;
  background: #edeef0;
  border-top: 1px solid #080232;
  margin: 10px 40px 0 0;
  cursor: pointer;

  &:nth-child(even) {
    background: #fff;
  }

  &:focus, &:active {
    background: #49a09d;
  }
}
.avatar {
  padding: 5px;
  margin-right: 25px;
  border-radius: 10px;
  box-shadow: 0px 2px 8px #080232;

  img {
    width: 90px;
    height: 90px;
    border-radius: 5px;
  }
}
.userDesc {
  line-height: 1.6;
  font-weight: 300;
  font-size: 15px;
}

@media (max-width: 419px) {
  .userItem {
    margin: 0;
  }
  .avatar {
    margin-right: 2%;
    img {
      width: 40px;
      height: 40px;
      border-radius: 5px;
    }
  }
  .userDesc {
    word-break: break-all;
  }
}

