* {
  box-sizing: border-box;
  margin: 0;
  padding:0;
  font-family: 'Montserrat', sans-serif;
}

.App {
  position: relative;
  width: 100%;
}

.page-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

