.wrapper {
  display: flex;
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}

table {
  max-width: 850px;
  width: 100%;
  text-align: center;
  border-bottom: 1px solid #dfdfdf;
  border-radius: 6px;
  border-spacing: 5px;
  border-collapse: collapse;
  overflow: hidden;
  background: linear-gradient(45deg, #49a09d, #080232);
  color: #fff;
}

th, td {
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.2);
}

th {
  text-align: left;
}

thead {
  background-color: #55608f;
  width: 100%;
}

td {
  padding: 15px;
  background-color: rgba(167, 144, 144, 0.2);
  color: #fff;

  .button {
    color: #fff;
    cursor: pointer;
  }

  span {
    cursor: pointer;
  }
}
@media (max-width: 367px) {
  th, td {
    padding: 1%;
    font-size: 15px;
    word-break: break-all;
    max-width: 50px;
  }
}



